.App {

  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(to bottom, transparent, transparent, transparent, rgba(93, 90, 90, 0.637), rgba(53, 52, 52, 0.637), black),
  url('./bg.jpg');
}
* {
  margin: 0;
  padding: 0;
  }
  .appName{
    font-size: 6vh;
    font-weight: 600;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding-left: 15vw;
    padding-top: 15vh;
    color: aliceblue;

    -webkit-animation: slideInToRight 0.7s forwards;
    -webkit-animation-delay: 0s;
    animation: slideInToRight 0.7s forwards;
    animation-delay: 0s;
  }
  @keyframes slideInToRight {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }
  .ytIco{
    color: rgb(190, 35, 35);
    -webkit-animation: infinite-spinning 3s infinite;
    -webkit-animation-delay: 1s;
    animation: infinite-spinning 3s infinite;
    animation-delay: 1s;
  }
  @keyframes infinite-spinning {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .inpDiv{
    padding-top: 20vh;
    display: flex;
    justify-content: center;
    -webkit-animation: slideTop 1.8s forwards;
    -webkit-animation-delay: 0s;
    animation: slideTop 1.8s forwards;
    animation-delay: 0s;
  }
  @keyframes slideTop {
    0% {
      transform: translateY(100%);
    }
    100% {
      transform: translateY(0);
    }
  }
  .img{
    height: 25vh;
    width: 20vw;
    
  }

.inp{
  font-size: 2vh;
  width: 28%;
  padding: .7vh;
  text-align: center;
}
.resultDiv{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 5vh;
  visibility: hidden;
}
.resultdownloadwrapperDiv{
  display: flex;
  flex-direction: column;
  margin-left: 2vw;
  margin-top: 3vh;
}
.btn{
  margin-top: 2vh;
  flex: 1 1 auto;
  margin: 2px;
  padding: 13px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 1vh #eee;
  border-radius: 10px;
  border: 3px solid transparent;
  margin-left: .8vw;
}
.btn:hover{
  cursor: pointer;

}

.btn-2 {
  font-weight: 600;
  color: whitesmoke;
  background-image: linear-gradient(to right, #3856b6 0%, #5470cc 51%,#405cb8 100%);
}
.downloadDiv{
  display: flex;
  justify-content: center;
  margin-top: 5vh;
  font-size: 3.8vh;
  color:white;
  visibility: hidden;
}
.downloadDiv:hover{
  cursor: pointer;

  font-weight: 600;

}
.downloadDiv:hover .dlIco{
  cursor: pointer;
  color: rgb(190, 35, 35);
  font-weight: 600;

}

.syncRot{
  color: rgb(197, 204, 98);
  -webkit-animation: infinite-spinning 1s infinite;
  -webkit-animation-delay: 1s;
  animation: infinite-spinning 1s infinite;
  animation-delay: 1s;
  font-size: 5vh;
}
.loadDiv{
  display: flex;
  justify-content: center;
  margin-top: 2vh;
}
.inpDiv .tooltiptext {

  width: 120px;
  background-color: rgb(71, 67, 67);
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  font-weight: 1.4vh;
  /* Position the tooltip text - see examples below! */
  top: -5px;
  left: 105%; 
  padding-top: 1vh;
}
